body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.avatar {
  border-radius: 50%;
}
.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}
.welcome-message {
  margin-top: 60px;
  padding: 25px;
  background-color: #282c34;
  text-align: center;
}
.welcome-message h1 {
  color: white;
}
.welcome-message p {
  color: whitesmoke;
}

.featured {
  background-color: greenyellow;
  padding: 20px;
  border-radius: 10px;
  text-align: center;
}

.featured h2 {
  font-size: 24;
  margin-bottom: 10px;
}

.featured p {
  font-size: 16px;
  margin-bottom: 8px;
}

.featured button {
  padding: 10px 20px;
  background-color: blue;
  color:white;
  border: none;
  border-radius: 5px;
  font-size: 18px;
  cursor: pointer;
}

.puzzle-showcase {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
  border: 2px solid black;
  padding: 10px;
}
.puzzle-image {
  margin-right: 20px;
}
.puzzle-image img {
  max-width: 350px;
  height: auto;
}

.puzzle-description {
  flex: 1 1;
  margin-right: 20px;
}

.puzzle-description h3 {
  font-size: 24px;
  margin-bottom: 10px;
}

.puzzle-description p {
  font-size: 16px;
  margin-bottom: 8px;
}
.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.header {
    position: fixed;
    top:0;
    left: 0;
    width: 100%;
    background-color: white;
    color: black;
    padding: 10px;
    z-index: 100;
  }
  
.logo {
    margin-top: 0px;
    height: 7vmin;
    pointer-events: none;
  }
.nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.nav ul {
    list-style: none;
    display: flex;
    margin: 0;
    padding: 0;
}

.nav li {
    margin-right: 25px;
  }
  
.nav a {
    text-decoration: none;
    color: black;
  }

.nav a:hover {
    text-decoration: underline;
  }

.nav-text    {
    margin-top: 25px;
}
